var rule_video = '';
var norequest = 1;
export default {
	data() {
		return {
			noData: 2, // 没有更多数据提示 1显示2隐藏
			showLoading: 2, // 加载更多提示 1显示 2隐藏
			noRecord: 2, // 没有记录提示 1显示 2隐藏
			catId: 0,
			'test': 1,
			'catIndex': 0,
			'videoPath': '',
			'videoStatus': 2,
			iStatusBarHeight: 0,
			shuomingStatus: 2,
			category_list: [],
			goodsLists: [],
			pages: 0,
			keyword: '',
		}
	},
	//监听页面加载，其参数为上个页面传递的数据，参数类型为Object（用于页面传参）
	onLoad(options) {
		var that = this;
		this.iStatusBarHeight = uni.getSystemInfoSync().statusBarHeight;

		if (options.jump) {
			console.log('跳转页面')
			// that.norequest = 1;

			uni.navigateTo({
				url: '/' + options.jump + '?' + options.jumpData1 + '=' + options.jumpData2 + "&" + options.jumpData3 + "=" +
					options.jumpData4 + "&" + options.jumpData5 + "=" + options.jumpData6,
				success: function(res) {

					// that.norequest = 2;

				}
			})
			// return;
		}

		that.getCategory();
	},
	//监听页面显示。页面每次出现在屏幕上都触发，包括从下级页面点返回露出当前页面
	onShow() {

	},
	//监听页面初次渲染完成。注意如果渲染速度快，会在页面进入动画完成前触发
	onReady() {},
	//监听页面隐藏
	onHide() {},
	//监听页面卸载
	onUnload() {},
	//监听用户下拉动作，一般用于下拉刷新
	onPullDownRefresh() {
		this.goodsLists = [];
		this.pages = 0;
		this.getCategory();
	},
	//页面滚动到底部的事件 常用于上拉加载下一页数据
	onReachBottom() {
		this.getGoodslist();
	},
	onShareAppMessage() {
		var that = this;
		var title = that.shareData.title;
		var path = '/pages/index/index?tid='+that.shareData.id;
		var imageUrl = that.shareData.image;
		return {
			title: title,
			path: path,
			imageUrl: imageUrl,
		}
	},
	methods: {
		getCategory() {
			var that = this;
			this.$http.post("goods/category", {
				'showLoading': false,
			}).then((res) => {
				//输出请求数据 
				that.rule_video = res.data.rule_video;
				that.shareData = res.data.share;
				var all = {
					id: null,
					name: '全部'
				};
				res.data.list.unshift(all);
				that.category_list = res.data.list;
				that.catId = res.data.list[0].id;
				that.getGoodslist();
			}).catch(err => {
				console.log('info,错误请求返回=', err)
			})
		},
		//自定义方法
		// 获取商品列表
		getGoodslist() {
			var that = this;
			that.showLoading = 1;
			this.$http.post("goods/index", {
				pages: that.pages,
				cid: that.catId
			}, {
				'showLoading': false
			}).then((res) => {
				//输出请求数据 
				if (that.pages == 0 && (res.data == '' || res.data == null)) {
					that.noRecord = 1;
					return;
				}
				if (res.data == '' || res.data == null) {
					that.noData = 1;
					that.showLoading = 2;
					return;
				}
				that.goodsLists = that.goodsLists.concat(res.data);
				that.pages += 1;
				that.showLoading = 2;
				if (res.data.length < 10) {
					that.noData = 1;
				}

			}).catch(err => {
				console.log('info,错误请求返回=', err)
			})
		},
		// 获取视频说明地址
		getShuomingVideo() {
			var that = this;
			console.log('获取视频说明地址')
			if (that.videoPath != '') {
				that.videoStatus = 1;
				return;
			}
			this.$http.post('getVideoPath', {
				id: that.rule_video
			}).then((res) => {
				console.log('视频地址', res);
				that.videoPath = res.data;
			})
			that.videoStatus = 1;
		},
		// 获取搜索关键字
		getinputval(e) {
			console.log('搜索关键字=', e.detail.value)
			this.keyword = e.detail.value;
		},
		searchGoods() {
			var that = this;
			if (that.keyword == '') {
				console.log('无需搜索')
				return;
			}
			that.goodsLists = [];
			that.pages = 0;
			that.showLoading = 1;
			this.$http.post('search', '', {
				'showLoading': false,
				headers: {
					'content-type': 'application/x-www-form-urlencoded',
					'keyword': encodeURI(that.keyword),
				}
			}).then((res) => {

				console.log('打印搜索结果', res);
				that.showLoading = 2;
				if (res.data == '' || res.data == null) {
					that.noRecord = 1;
					return;
				}
				that.goodsLists = res.data;
				that.noData = 1;

			})
		},
		// 切换分类
		changegoodstype(e) {
			var that = this;
			var index = e.currentTarget.dataset.index;
			var catId = e.currentTarget.dataset.id;

			if ((that.catIndex == index) && that.pages != 0) {
				return false;
			}
			if (that.keyword != '') {
				that.keyword = '';
			}

			that.goodsLists = [];
			that.pages = 0;
			that.catIndex = index;
			that.catId = catId;
			that.noData = 2;
			that.noRecord = 2;
			that.showLoading = 2;

			that.getGoodslist();
		},
		jumpTo(e) {
			var jumptype = e.currentTarget.dataset.jumptype;
			var url = e.currentTarget.dataset.url;
			var closepage = e.currentTarget.dataset.closepage;
			this.jumpFunction(jumptype, url, closepage);
		},
		closeVideo() {
			var that = this;
			if (that.videoStatus == 1) {
				that.videoStatus = 2;
			} else {
				that.videoStatus = 1;
			}
		},
		cshuomingStatus() {
			console.log('切换说明显示状态')
			var that = this;
			if (that.shuomingStatus == 1) {
				that.shuomingStatus = 2;
			} else {
				that.shuomingStatus = 1;
			}
		},


	}
}
